import React, { useState } from "react";

import {
  ButtonBase,
  ButtonGroup,
  CardActionArea,
  CardActions,
  CardContent,
  Grid,
  Typography,
} from "@material-ui/core";

import { Button, Card as MuiCard } from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";
import ImageBox from "./ImageBox";

import crestImg from "../img/gr/birb.jpg";

const useStyles = makeStyles({
  body: {
    margin: "0 5px",
    padding: 5,
    textAlign: "left",
  },
  linkArea: {
    margin: 0,
    padding: 0,
  },
  fullWidth: {
    width: "100%",
  },
  link: {
    width: "100%",
  },
});

function NoteCard(props) {
  const classes = useStyles();

  const linkClicked = () => {
    props.feedControl();
  };

  return (
    <div>
      <div className={classes.body}>
        <Typography variant="subtitle1" color="textSecondary" component="h4">
          {props.cardData.name}
        </Typography>
        <Typography varient="asd" component="p">
          "{props.cardData.description}"
        </Typography>
      </div>
      <CardActions className={classes.linkArea}>
        <ButtonGroup size="small" variant="text" className={classes.fullWidth}>
          <Button className={classes.link} color="">
            More Info
          </Button>
        </ButtonGroup>
      </CardActions>
    </div>
  );
}

NoteCard.defaultProps = {
  edit: false,
};
export default NoteCard;
