import { IconButton, makeStyles, TextField } from "@material-ui/core";
import { Close, Search } from "@material-ui/icons";
import React, { useState } from "react";
import Colors from "../themes/colors.js";

const useStyles = makeStyles({
  searchOpen: {
    transition : "width 350ms",
    background: `${Colors.tp}`,
    color: "white",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    paddingLeft: 10,
  },
  input: {
    color: `${Colors.li}`,
    width: "66vw",
  },
  label: {
    color: `${Colors.md}`,
  },
});

function SearchBtn(props) {
  const [searchToggle, setSearchToggle] = useState(false);
  const [searchState, setSearchState] = useState("");
  const classes = useStyles();

  const toggleSearch = () => {
    let search = searchToggle;
    setSearchState("");
    setSearchToggle(!search);
  };

  const searchClicked = () => {
    toggleSearch();
  };

  const searchControl = (input) => {
    let newVal = searchState;
    if (input === "Backspace") {
      newVal = newVal.substring(0, newVal.length - 1);
    } else if (input === "Enter") {
      // Submit to Results Page
      props.bodyControl("Search");
      props.searchControl(searchState);
      toggleSearch();
    } else {
      if (input.length === 1) {
        newVal += input;
      }
    }
    setSearchState(newVal);
  };

  const keyPress = (e) => {
    searchControl(e.key);
  };

  let output = <p>Error</p>;
  if (searchToggle) {
    output = (
      <div className={classes.searchOpen}>
        <TextField
          size="small"
          InputLabelProps={{
            className: classes.label,
          }}
          InputProps={{
            className: classes.input,
          }}
          label="Search"
          color="secondary"
          onKeyDown={keyPress}
        />
        <IconButton color="inherit" onClick={searchClicked}>
          <Close />
        </IconButton>
      </div>
    );
  } else {
    output = (
      <IconButton onClick={searchClicked}>
        <Search />
      </IconButton>
    );
  }

  return <div>{output}</div>;
}
export default SearchBtn;
