import React from "react";

import { Card as MuiCard } from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";
import ProjectCard from "./ProjectCard";
import GroupCard from "./GroupCard";

const useStyles = makeStyles({
  root: {
    maxWidth: 345,
    width: "100%",
    margin: "10px auto",
    border: "1px solid grey",
    overflow: "Hidden",
    borderRadius: 5,
  },
});

function Card(props) {
  const classes = useStyles();

  let cardContent = (
    <GroupCard cardData={props.cardData} feedControl={props.feedControl} />
  );

  if (props.cardType === "Project") {
    cardContent = (
      <ProjectCard cardData={props.cardData} feedControl={props.feedControl} />
    );
  }

  return <MuiCard className={classes.root}>{cardContent}</MuiCard>;
}

export default Card;
