import React from "react";

import Logo from "../logo.svg";

// use an iconButton with an icon inside, pass the onClick function to it

import { IconButton } from "@material-ui/core";
import { KeyboardBackspace, Home } from "@material-ui/icons";

function AddBtn(props) {
  const goBack = () => {
    if (props.bodyState === "Feed") {
      props.feedControl(-1);
    } else if (props.bodyState === "Chats") {
      props.chatControl(-1);
    }
  };

  let icon = <KeyboardBackspace />;

  if (props.bodyState === "Feed") {
    if (props.feedState === 0) {
      // use the projecta logo
      icon = <Home />;
    }
  } else if (props.bodyState === "Chats") {
    if (props.chatState === 0) {
      // use the projecta logo
      icon = <Home />;
    }
  } else if (props.bodyState === "Account") {
    // use the projecta logo
    icon = <Home />;
  } else if (props.bodyState === "Search") {
    icon = <Home />;
  }

  return (
    <IconButton className={props.class} color="default" onClick={goBack}>
      {icon}
    </IconButton>
  );
}

AddBtn.defaultProps = {
  onClick: null,
};

export default AddBtn;
