import React from "react";

import { Card as MuiCard, Divider } from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";
import TaskCard from "./TaskCard";
import NoteCard from "./NoteCard";
import ConvoCard from "./ConvoCard";
import ProfileCard from "./ProfileCard";
import SearchCard from "./SearchCard";

const useStyles = makeStyles({
  root: {
    width: "100%",
    overflow: "Hidden",
  },
});

function CardTight(props) {
  const classes = useStyles();

  let cardContent = (
    <TaskCard cardData={props.cardData} feedControl={props.feedControl} />
  );

  if (props.cardType === "Note") {
    cardContent = (
      <NoteCard cardData={props.cardData} feedControl={props.feedControl} />
    );
  } else if (props.cardType === "Convo") {
    cardContent = (
      <ConvoCard cardData={props.cardData} chatControl={props.chatControl} />
    );
  } else if (props.cardType === "Profile") {
    cardContent = (
      <ProfileCard cardData={props.cardData} chatControl={props.chatControl} />
    );
  } else if (props.cardType === "Search") {
    cardContent = (
      <SearchCard cardData={props.cardData} bodyControl={props.bodyControl} />
    );
  }

  return (
    <MuiCard className={classes.root}>
      {cardContent}
      <Divider />
    </MuiCard>
  );
}

export default CardTight;
