import {
  Button,
  Divider,
  InputLabel,
  makeStyles,
  TextField,
  ThemeProvider,
  Typography,
} from "@material-ui/core";
import React from "react";
import Logo from "../img/logo.svg";
import { GreenRed } from "../themes/themes";
import Colors from "../themes/colors";
import ImageBox from "./ImageBox";

const useStyles = makeStyles({
  root: {
    width: "100%",
    paddingBottom: "10%",
    height: "100%",
    background: `${Colors.bg}`,
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
  },
  form: {
    width: "80%",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    margin: " 0 10%",
  },
  input: {
    margin: "10px",
    width: "100%",
    background: "#f3f3f3",
    border: "2px solid #e3e3e3",
    padding: 5,
    borderRadius: 5,
  },
  btn: {
    margin: 10,
    width: "80%",
  },
  label: {
    marginLeft: 15,
    textAlign: "left",
    width: "100%",
  },
  header: {
    fontSize: "1.8rem",
    textAlign: "center",
    margin: "15px auto 50px",
  },
});

function LoginPage(props) {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <ImageBox img={Logo} height={150} />
      <Typography variant="h4" component="h1" className={classes.header}>
        Login
      </Typography>
      <div className={classes.form}>
        <Typography className={classes.label} variant="subtitle1" component="p">
          Username
        </Typography>
        <TextField className={classes.input} />
        <Typography className={classes.label} variant="subtitle1" component="p">
          Password
        </Typography>
        <TextField className={classes.input} />
        <Button
          variant="contained"
          color="primary"
          onClick={props.modalControl}
          className={classes.btn}
        >
          Log In
        </Button>
        <ThemeProvider theme={GreenRed}>
          <Button
            variant="contained"
            color="primary"
            onClick={props.modalControl}
            className={classes.btn}
          >
            Register
          </Button>
        </ThemeProvider>
      </div>
    </div>
  );
}

export default LoginPage;
