import React, { useState } from "react";

import { Container, makeStyles } from "@material-ui/core";

import Card from "./Card";
import CardTight from "./CardTight";
import GroupHead from "./GroupHead";
import ProjectHead from "./ProjectHead";
import TaskHead from "./TaskHead";

const useStyles = makeStyles(() => ({
  root: {
    padding: 0,
    background: "#c6c8c9",
  },
  heading: {
    margin: "10px",
    textAlign: "center",
  },
  cardList: {
    width: "100%",
    margin: "auto",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
  },
}));

// can display either an array, or a loading message

function Feed(props) {
  const classes = useStyles();
  let feedHTML = <p>Nope</p>;
  if (props.feedState == 0) {
    feedHTML = (
      <Container maxWidth="sm" className={classes.root}>
        <h3 className={classes.heading}>My Groups</h3>
        {props.dataState.groups.map((item) => {
          return (
            <Card
              cardType="Group"
              cardData={item}
              feedControl={props.feedControl}
            />
          );
        })}
      </Container>
    );
  } else if (props.feedState == 1) {
    feedHTML = (
      <Container maxWidth="sm" className={classes.root}>
        <GroupHead title="This Group" headData={props.dataState.groups[0]} />
        <div className={classes.cardList}>
          {props.dataState.projects.map((item) => {
            return (
              <Card
                key={item.id}
                cardType="Project"
                cardData={item}
                feedControl={props.feedControl}
              />
            );
          })}
        </div>
      </Container>
    );
  } else if (props.feedState == 2) {
    feedHTML = (
      <Container maxWidth="sm" className={classes.root}>
        <ProjectHead
          title="This Project"
          headData={props.dataState.projects[0]}
        />
        {props.dataState.tasks.map((item) => {
          return (
            <CardTight
              key={item.id}
              cardType="Task"
              cardData={item}
              feedControl={props.feedControl}
            />
          );
        })}
      </Container>
    );
  } else if (props.feedState == 3) {
    feedHTML = (
      <Container className={classes.root}>
        <TaskHead
          title="This Task"
          headData={props.dataState.tasks[0]}
        />
        {props.dataState.notes.map((item) => {
          return (
            <CardTight
              key={item.id}
              cardType="Note"
              cardData={item}
              feedControl={props.feedControl}
            />
          );
        })}
      </Container>
    );
  }

  return feedHTML;
}

export default Feed;
