import Header from "./components/Header";
import Body from "./components/Body";
import BottomNav from "./components/BottomNav";
import Modal from "./components/Modal";

import "./css/main.css";
import { ThemeProvider } from "@material-ui/core";
import { MainTheme } from "./themes/themes";

import React, { useState, useEffect } from "react";

import defaultData from "./data";

// imported a getall from the model, importing models loooks like a great idea

// looks pretty good i think
// could have put more components in here directly
function App() {
  // set the states
  // feedstate works well
  // modalstate is great, this is the right way to do it

  const [dataState, setdataState] = useState(defaultData);
  const [bodyState, setBodyState] = useState("Feed");
  const [chatState, setChatState] = useState(0);
  const [feedState, setFeedState] = useState(0);
  const [modalState, setmodalState] = useState([false, "", {}]);
  const [searchState, setSearchState] = useState("");

  // const dataControl = (input = defaultData) => {
  //   setdataState(input);
  // };

  const bodyControl = (input) => {
    setBodyState(input);
  };

  const searchControl = (input = "") => {
    setSearchState(input);
  };

  // default sets it to closed with no data
  const modalControl = (open = false, mode = "", data = {}) => {
    setmodalState([open, mode, data]);
  };

  // makes sure the new array is noticed as new to ensure state refresh
  const feedControl = (input = 1) => {
    let output = feedState;
    output += input;
    // sets the input array to be the state
    if (output < 0) {
      output = 0;
    }
    if (output > 3) {
      output = 3;
    }
    setFeedState(output);
  };

  // makes sure the new array is noticed as new to ensure state refresh
  const chatControl = (input = 1) => {
    let output = chatState;
    output += input;
    // sets the input array to be the state
    if (output < 0) {
      output = 0;
    }
    if (output > 1) {
      output = 1;
    }
    setChatState(output);
  };

  // passes the setfeedstate function stack to the model

  // handles page load Get
  useEffect(() => {
    // check if the data is empty, if so call the API

    console.log("Render");
  }, []);

  return (
    <div className="App">
      <ThemeProvider theme={MainTheme}>
        <Header
          modalControl={modalControl}
          bodyControl={bodyControl}
          searchControl={searchControl}
          bodyState={bodyState}
        />
        <Body
          dataState={dataState}
          bodyState={bodyState}
          feedState={feedState}
          chatState={chatState}
          searchState={searchState}
          feedControl={feedControl}
          chatControl={chatControl}
        />
        <BottomNav
          bodyState={bodyState}
          feedState={feedState}
          chatState={chatState}
          bodyControl={bodyControl}
          feedControl={feedControl}
          chatControl={chatControl}
        />
        <Modal modalState={modalState} modalControl={modalControl} />
      </ThemeProvider>
    </div>
  );
}

export default App;
