import React from "react";
import SearchHead from "./SearchHead";

import CardTight from "./CardTight";

function Search(props) {
  let resultArr = props.dataState.searchResults;
   console.log(resultArr);
  return (
    <div>
      <SearchHead term={props.searchState} />
      {resultArr.map((item) => {return (
         <CardTight cardType="Search" cardData={item} />
      )})}
    </div>
  );
}

export default Search;
