import { Grid, makeStyles, Typography } from '@material-ui/core'
import React from 'react'

const useStlyes = makeStyles({
   root : {
      padding : 10
   }
})

// Take an obj make Tabs 
// use material ui for tabs

function Stats() {
   const classes = useStlyes();
   return (
      <div className={classes.root}>
         <Grid container>
            <Grid item xs={4} >
               <Typography variant="body2" component="p" >
                  Stat 1
               </Typography>
            </Grid>
            <Grid item xs={8} >
               <Typography variant="body2" component="p" >
                  126 210 210
               </Typography>
            </Grid>
            <Grid item xs={4} >
               <Typography variant="body2" component="p" >
                  Stat 1
               </Typography>
            </Grid>
            <Grid item xs={8} >
               <Typography variant="body2" component="p" >
                  126 210 210
               </Typography>
            </Grid>
            <Grid item xs={4} >
               <Typography variant="body2" component="p" >
                  Stat 1
               </Typography>
            </Grid>
            <Grid item xs={8} >
               <Typography variant="body2" component="p" >
                  126 210 210
               </Typography>
            </Grid>
         </Grid>
      </div>
   )
}

export default Stats
