import React, { useState } from "react";

import {
  CardActionArea,
  Grid,
  Typography,
  CircularProgress,
} from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";
import ImageBox from "./ImageBox";

import projImg1 from "../img/pr/code.jpg";
import projImg2 from "../img/pr/code.jpg";
const useStyles = makeStyles({
  body: {
    margin: "0 5px",
    padding: 5,
    textAlign: "left",
  },
  fullWidth: {
    width: "100%",
  },
  wheel: {
    margin: "auto",
  },
  flexBox: {
    flexDirection: "row",
    display: "flex",
  },
  progress: {
    height: "100%",
    display: "flex",
    flexDirection: "column",
    alignItems: "cener",
    justifyContent: "center",
    textAlign: "center",
  },
});

function ProjectCard(props) {
  const classes = useStyles();

  const linkClicked = () => {
    props.feedControl();
  };

  return (
    <div>
      <CardActionArea
        className={(classes.body, classes.fullWidth)}
        component="button"
        onClick={linkClicked}
      >
        <div className={classes.flexBox}>
          <Grid container>
            <Grid item xs={4}>
                <ImageBox width={"100%"} img={projImg1} />
            </Grid>
            <Grid item xs={8} container>
              <Grid item xs={8}>
                <Typography variant="h6" color="textSecondary" component="h4">
                  {props.cardData.name}
                </Typography>
                <Typography
                  variant="body2"
                  color="textSecondary"
                  component="h4"
                >
                  {props.cardData.description}
                </Typography>
              </Grid>
              <Grid item xs={4} className={classes.progress}>
                <CircularProgress
                  variant="determinate"
                  value={props.cardData.completed}
                  className={classes.wheel}
                />
                <Typography varient="subtitle2" component="p">
                  15/17/2028
                </Typography>
              </Grid>
            </Grid>
          </Grid>
        </div>
      </CardActionArea>
    </div>
  );
}

ProjectCard.defaultProps = {
  edit: false,
};
export default ProjectCard;
