import React from "react";

import Chats from "./Chats";
import Feed from "./Feed";
import Profile from "./Profile";
import Search from "./Search";
import { makeStyles } from "@material-ui/core"

import Colors from "../themes/colors"

const useStyles = makeStyles({
  root : {
    minHeight : "calc(100vh - 110px)",
    margin: "48px 0 60px 0",
    background : `${Colors.bg}`
  }
})

function Body(props) {
  const classes = useStyles();
  let contents = (
      <Feed
        feedState={props.feedState}
        dataState={props.dataState}
        feedControl={props.feedControl}
      />
  );
  if (props.bodyState === "Account") {
    contents = (
        <Profile dataState={props.dataState} />
    );
  } else if (props.bodyState === "Chats") {
    contents = (
        <Chats dataState={props.dataState} chatControl={props.chatControl} chatState={props.chatState} />
    );
  } else if (props.bodyState === "Search") {
    contents = (
        <Search dataState={props.dataState} feedControl={props.feedControl} searchState={props.searchState}/>
    );
  }

  return <div className={classes.root}>{contents}</div>;
}

export default Body;
