import React, { useState } from "react";

import {
  ButtonGroup,
  CardActionArea,
  CardActions,
  Typography,
  Grid,
} from "@material-ui/core";

import { Card as MuiCard } from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";
import ImageBox from "./ImageBox";

import crestImg from "../img/gr/birb.jpg";

const useStyles = makeStyles({
  body: {
    maxWidth: "100vw",
    width: "100%",
    padding: 5,
  },
  linkArea: {
    margin: 0,
    padding: 0,
  },
  fullWidth: {
    width: "100%",
  },
  link: {
    width: "50%",
  },
});

function ConvoCard(props) {
  const classes = useStyles();

  const linkClicked = () => {
   props.chatControl();
  };

  return (
    <div>
      <CardActionArea component="button" onClick={linkClicked}>
        <Grid container>
          <Grid item xs={2}></Grid>
          <Grid item xs={10}>
            <div className={classes.body}>
              <Typography
                variant="subtitle1"
                color="textSecondary"
                component="h4"
              >
                {props.cardData.user}
              </Typography>
              <Typography varient="body1" component="p">
                "{props.cardData.message}"
              </Typography>
            </div>
          </Grid>
        </Grid>
      </CardActionArea>
    </div>
  );
}

ConvoCard.defaultProps = {
  edit: false,
};
export default ConvoCard;
