import { AppBar, Grid, makeStyles , IconButton} from "@material-ui/core";
import { Search , Menu } from "@material-ui/icons";

import React, { useState, useEffect } from "react";

import BackBtn from "./BackBtn";
import SearchBtn from "./SearchBtn";

// only mount the button for the modal not the modal itself
// position static, sets it as a regular dom element

const useStyles = makeStyles({
  root: {
    height: "fit-content",
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
});

function Header(props) {
  const [searchState, setsearchState] = useState(false);
  const classes = useStyles();

  const openModel = () => {
    props.modalControl(true);
  }

  return (
    <AppBar color="primary" position="static" className={classes.root} position="fixed">
      <IconButton onClick={openModel}> <Menu /> </IconButton>
      <SearchBtn bodyControl={props.bodyControl} searchControl={props.searchControl}/>
    </AppBar>
  );
}

export default Header;
