import { createTheme } from "@material-ui/core/styles";
import Typography from "./typography";
import colors from "./colors.js"

// helpful for consistancy and ease of use
// used them well


export const MainTheme = createTheme({
  palette: {
    primary: {
      main: `${colors.prim}`,
    },
    secondary: {
      main: `${colors.sec}`,
    },
    // type: "dark",
  },
  Typography,
});

export const GreenRed = createTheme({
  palette: {
    primary: {
      main: "#5fd677",
    },
    secondary: {
      main: "#da4a4a",
    },
    // type: "dark",
  },
  Typography,
});

export const BlueRed = createTheme({
  palette: {
    primary: {
      main: "#0077ff",
    },
    secondary: {
      main: "#da4a4a",
    },
    // type: "dark",
  },
  Typography,
});
