import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import BottomNavigation from "@material-ui/core/BottomNavigation";
import BottomNavigationAction from "@material-ui/core/BottomNavigationAction";

import {
  ChatBubble,
  PlaylistAddCheck,
  Settings,
  Person,
} from "@material-ui/icons";
import BackBtn from "./BackBtn";

const useStyles = makeStyles({
  root: {
    width: "100%",
    position: "fixed",
    bottom: 0,
  },
  btn: {
    width: "25%",
    flex : 1,
    minWidth : 30,
    maxWidth : 200,
  }
});

// get the feedcontroller and modal controller from the app
// set the onchange toe handle the change of state

export default function BottomNAv(props) {
  const classes = useStyles();

  const handleChange = (event, newValue) => {
    if (newValue == "Menu") {
      props.modalControl(true, "Menu");
    } else {
      props.bodyControl(newValue);
    }
  };

  return (
    <BottomNavigation
      value={props.bodyState}
      onChange={handleChange}
      className={classes.root}
      >
      <BottomNavigationAction
        label="Feed"
        value="Feed"
        icon={<PlaylistAddCheck />}
        className={classes.btn}
        />
      <BottomNavigationAction
        label="Chats"
        value="Chats"
        icon={<ChatBubble />}
        className={classes.btn}
        />
      <BottomNavigationAction
        label="Account"
        value="Account"
        icon={<Person />}
        className={classes.btn}
        />
      <BackBtn
        bodyState={props.bodyState}
        feedControl={props.feedControl}
        chatState={props.chatState}
        chatControl={props.chatControl}
        feedState={props.feedState}
        class={classes.btn}
        />
      ;
    </BottomNavigation>
  );
}
