import React, { useState } from "react";

import {
  ButtonGroup,
  CardActionArea,
  CardActions,
  Typography,
  Grid,
} from "@material-ui/core";

import { Card as MuiCard } from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";
import ImageBox from "./ImageBox";

import crestImg from "../img/gr/birb.jpg";

const useStyles = makeStyles({
  body: {
    maxWidth: "100vw",
    width: "100%",
    padding: 5,
  },
  linkArea: {
    margin: 0,
    padding: 0,
  },
  fullWidth: {
    width: "100%",
  },
  link: {
    width: "50%",
  },
  data: {
    fontWeight: "bold",
    marginLeft : 10
  },
});

function ProfileCard(props) {
  const classes = useStyles();

  const linkClicked = () => {
    console.log(props.cardData);
  };

  return (
    <div>
      <CardActionArea component="button" onClick={linkClicked}>
        <div className={classes.body}>
          <Typography variant="subtitle1" color="textSecondary" component="h4">
            {props.cardData.name}
          </Typography>
          <Typography className={classes.data} varient="body1" component="p">
            {props.cardData.data}
          </Typography>
        </div>
      </CardActionArea>
    </div>
  );
}

ProfileCard.defaultProps = {
  edit: false,
};
export default ProfileCard;
