import {
  Divider,
  makeStyles,
  Typography,
} from "@material-ui/core";
import React, { useState } from "react";

import ImageBox from "./ImageBox";
import HeadBanner from "./HeadBanner";

import crestImg from "../img/gr/birb.jpg";

const useStyles = makeStyles({
  root: {
    background: "white",
  },
  heading: {
    textAlign: "center",
    marginTop: 10,
  },
  fullWidth: {
    width: "100%",
  },
  pinnedMessage : {
    background : "#67a5d6",
    color: "white",
    padding : 10,
  }
});

function ProfileHead(props) {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Typography className={classes.heading} component="h5" variant="h5">
        This User
      </Typography>
      <ImageBox height={120} img={crestImg} />
      <Divider />
      <HeadBanner content="Settings"/>
    </div>
  );
}

export default ProfileHead;
