import React from "react";
import Stats from "./Stats";

function GroupInfoArea(props) {
  let content = <Stats />;

  if (props.infoState == 1) {
    content = (
      <div>
        <p>Users</p>
        <Stats />
      </div>
    );
  } else if (props.infoState == 2) {
    content = (
      <div>
        <p>Dates</p>
        <Stats />
      </div>
    );
  }
  return <div>{content}</div>;
}

export default GroupInfoArea;
