// Copied from the material ui docs and modified
import React from "react";

// import it with a different name, prevents a clash with my file name
import {
  Button,
  ButtonGroup,
  Divider,
  makeStyles,
  Modal as MuiModal,
  ThemeProvider,
} from "@material-ui/core";
import Colors from "../themes/colors";
// modal is imported to the app level and controlled by state
// is used to house the create and delete forms
import { BlueRed } from "../themes/themes";
// import the modal contents
import LoginPage from "./LoginPage";

const useStyles = makeStyles({
  root: {
    position: "absolute",
    left: "16%",
    top: "25%",
    background: `${Colors.li}`,
    color: `${Colors.dk}`,
    width: "66%",
    padding: "12px 16px",
    borderRadius: 10,
  },
  group: {
    display: "flex",
    flexDirection: "column",
  },
  btn: { margin: 5 },
  divider: {
    margin: 10,
  },
});

const Modal = (props) => {
  const classes = useStyles();
  // getModalStyle is not a pure function, we roll the style only on the first render

  // set the content to be the add form by default


const logout = () => {
  props.modalControl(true , "Login");
}

  let content = (
    <div className={classes.root}>
      <ThemeProvider theme={BlueRed}>
        <ButtonGroup
          className={classes.group}
          color="default"
          variant="outlined"
          rounded
        >
          <Button className={classes.btn} color="primary">
            Refresh
          </Button>
          <Divider className={classes.divider} />
          <Button className={classes.btn}>Blah</Button>
          <Button className={classes.btn}>Blah</Button>
          <Button className={classes.btn}>Settings</Button>
          <Button className={classes.btn}>Menu</Button>
          <Divider className={classes.divider} />
          <Button className={classes.btn} color="secondary" onClick={logout}>
            Log out
          </Button>
        </ButtonGroup>
      </ThemeProvider>
    </div>
  );

  if (props.modalState[1] === "Login") {
    content = <LoginPage modalControl={props.modalControl}/>;
  }

  // modalcontrol with no params sets it to closed
  const setClose = () => {
    props.modalControl();
  };

  return (
    <div>
      <MuiModal
        open={props.modalState[0]}
        onClose={setClose}
        aria-labelledby="Settings Modal"
        aria-describedby="Provides a Menu"
      >
        {content}
      </MuiModal>
    </div>
  );
};

Modal.defaultProps = {};

export default Modal;
