import React, { useState } from "react";

import {
  ButtonBase,
  ButtonGroup,
  CardActionArea,
  CardActions,
  CardContent,
  Grid,
  Typography,
} from "@material-ui/core";

import { Button, Card as MuiCard } from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";
import ImageBox from "./ImageBox";

import crestImg from "../img/gr/birb.jpg"

const useStyles = makeStyles({
  body: {
    margin: "0 5px",
    padding: 5,
    textAlign: "left",
  },
  linkArea: {
    margin: 0,
    padding: 0,
  },
  fullWidth: {
    width: "100%",
  },
  link: {
    width: "50%",
  },
});

function GroupCard(props) {
  const classes = useStyles();

  const linkClicked = () => {
    props.feedControl();
  };

  return (
    <div>
      <CardActionArea
        className={(classes.body, classes.fullWidth)}
        component="button"
        onClick={linkClicked}
      >
        <Grid container className={classes.body}>
          <Grid item xs={8}>
            <Typography variant="h5" color="textSecondary" component="h4">
              {props.cardData.name}
            </Typography>
            <Typography varient="asd" component="p">
              "{props.cardData.pinnedMessage}"
            </Typography>
          </Grid>
          <Grid item xs={4}>
            <ImageBox img={crestImg} height={100} />
          </Grid>
        </Grid>
      </CardActionArea>
      <CardActions className={classes.linkArea}>
        <ButtonGroup variant="contained" className={classes.fullWidth}>
          <Button className={classes.link} size="small" color="primary">
            Members
          </Button>
          <Button className={classes.link} size="small" color="secondary">
            Info
          </Button>
        </ButtonGroup>
      </CardActions>
    </div>
  );
}

GroupCard.defaultProps = {
  edit: false,
};
export default GroupCard;
