import {
  Button,
  ButtonGroup,
  Divider,
  makeStyles,
  Typography,
} from "@material-ui/core";
import React, { useState } from "react";

import ImageBox from "./ImageBox";
import HeadBanner from "./HeadBanner";
import GroupInfoArea from "./GroupInfoArea";

import crestImg from "../img/gr/birb.jpg";

const useStyles = makeStyles({
  root: {
    background: "white",
  },
  heading: {
    textAlign: "center",
    marginTop: 10,
  },
  listHead: {
    marginBottom: "10px 0",
    background: "#8b46bf",
    color: "white",
    padding: 10,
  },
  fullWidth: {
    width: "100%",
  },
  pinnedMessage : {
    background : "#67a5d6",
    color: "white",
    padding : 10,
  }
});

function GroupHead(props) {
  const [infoState, setinfoState] = useState(0);

  const infoControl = (number = 0) => {
    setinfoState(number);
  };

  const infoButtonClicked = (e) => {
    infoControl(e.currentTarget.value);
  };

  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Typography className={classes.heading} component="h5" variant="h5">
        This Group
      </Typography>
      <ImageBox height={120} img={crestImg} />
      <Divider />
      <ButtonGroup
        size="small"
        variant="contained"
        className={classes.fullWidth}
      >
        <Button
          value={0}
          variant="text"
          className={classes.fullWidth}
          color=""
          onClick={infoButtonClicked}
        >
          Info
        </Button>
        <Button
          value={1}
          className={classes.fullWidth}
          color="primary"
          onClick={infoButtonClicked}
        >
          Users
        </Button>
        <Button
          value={2}
          className={classes.fullWidth}
          color="primary"
          onClick={infoButtonClicked}
        >
          Dates
        </Button>
      </ButtonGroup>
      <GroupInfoArea infoState={infoState} />
      <Typography className={classes.pinnedMessage} >
       "{props.headData.pinnedMessage}""
      </Typography>
      <HeadBanner content="Projects" class={classes.listHead} />
    </div>
  );
}

export default GroupHead;
