import { makeStyles } from "@material-ui/core";
import React from "react";

import CardTight from "./CardTight";
import ChatBubble from "./ChatBubble";
import Color from "../themes/colors";

const useStyles = makeStyles({
  root: {},
  msgFeed: {
    background: `${Color.bg}`,
  },
});

function Chats(props) {
  const classes = useStyles();
  let convoArr = props.dataState.conversations;
  let messageArr = props.dataState.messages;
  let outPut = (
    <div>
      {convoArr.map((item) => {
        return (
          <CardTight
            key={item.user}
            cardData={item}
            cardType="Convo"
            chatControl={props.chatControl}
          />
        );
      })}
    </div>
  );

  if (props.chatState === 1) {
    outPut = (
      <div className={`flex flex-col ${classes.msgFeed}`}>
        {messageArr.map((item) => {
          return <ChatBubble message={item[0]} sender={item[1]} />;
        })}
      </div>
    );
  }

  return <div>{outPut}</div>;
}

export default Chats;
