import React, { useState } from "react";

import {
  ButtonBase,
  ButtonGroup,
  CardActionArea,
  CardActions,
  CardContent,
  Grid,
  Typography,
} from "@material-ui/core";

import { Button, Card as MuiCard } from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";
import ImageBox from "./ImageBox";

import crestImg from "../img/gr/birb.jpg";

const useStyles = makeStyles({
  body: {
    maxWidth : "100vw",
    width : "100%",
    padding : 5,
  },
  linkArea: {
    margin: 0,
    padding: 0,
  },
  fullWidth: {
    width: "100%",
  },
  link: {
    width: "50%",
  },
});

function TaskCard(props) {
  const classes = useStyles();

  const linkClicked = () => {
    props.feedControl();
  };

  return (
    <div>
      <CardActionArea
  
        component="button"
        onClick={linkClicked}
      >
        <div className={classes.body}>
          <Typography variant="h5" color="textSecondary" component="h4">
            {props.cardData.name}
          </Typography>
          <Typography varient="asd" component="p">
            "{props.cardData.description}"
          </Typography>
        </div>
      </CardActionArea>
      <CardActions className={classes.linkArea}>
        <ButtonGroup
          size="small"
          variant="contained"
          className={classes.fullWidth}
        >
          <Button className={classes.link} color="primary">
            Notes
          </Button>
          <Button className={classes.link}color="secondary">
            Info
          </Button>
        </ButtonGroup>
      </CardActions>
    </div>
  );
}

TaskCard.defaultProps = {
  edit: false,
};
export default TaskCard;
